@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
/* body style */
body {
    font-family: Raleway, sans-serif;
    margin: 0;
    font-size: 1rem;
    text-align: center;
    vertical-align: baseline;
}

#quote-box {
    max-width: 600px;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
}
.text-and-icon{
    width: 550px;
    margin: 20px;
    overflow-wrap: break-word;
    font-weight: 500;
}
#text {
    font-size: 28px;
    display: inline;
    text-align: left;
}

.quote-icon{
    text-align: left;
    margin-right: 10px;
    font-size: 25px;
}

#author{
    margin: 20px 20px 10px;
    font-weight: 500;
    text-align: right;
    font-size: 16px;
}

#new-quote{
    padding: 13px 14px;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
}

#new-quote:hover{
    color: white;
    cursor:pointer;
}

.tweet-button {
    max-width: 50px;
    padding: 12px 14px 0;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 18px;
}

#tweet-quote {
    text-decoration: none;
    padding: 12px 12px 8px;
    border-radius: 5px;
}

.button-wraper{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin: 40px 20px 10px;
}






